@font-face {
	font-family: "OldStandard";
	font-weight: normal;
	font-style: normal;
	src: url(/static/media/OldStandard-Regular.cb974098.ttf);
}

@font-face {
	font-family: "OldStandard";
	src: url(/static/media/OldStandard-Bold.11976ea1.ttf);
	font-weight: bold;
	font-style: bold;
}

@font-face {
	font-family: "OldStandard";
	src: url(/static/media/OldStandard-Italic.6b56f555.ttf)y;
	font-weight: 100;
	font-style: 100;
}

html,
body {
	min-width: 100%;
	margin: 0;
	padding: 0;
}
body {
	overflow-y: auto;
	overflow-x: hidden;
  font-family: "OldStandard", sans-serif !important;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

h1 { font-size: 44px; margin: .67em 0; font-weight: 100;}
h2 { font-size: 1.8em; margin: .75em 0 }
h3 { font-size: 1.17em; margin: .83em 0 }
h4 { margin: 1.12em 0 }
h5 { font-size: .83em; margin: 1.5em 0 }
h6 { font-size: .75em; margin: 1.67em 0 }

h3 {
  font-size: 24px;
  font-weight: 100;
}
p {
  font-size: 16px;
}

.App {
  text-align: center;
  margin-bottom: 40px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App__header {
  background: #282c34 url(/static/media/WeddingInvitePaint.263db6bb.png) no-repeat center -140px;
  background-size: cover;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}
.App__nav {
  width: calc(100% - 120px);
  padding: 20px;
}

.App__nav--collapsed {
  height: 0px;
  -webkit-transition: height 0.25s;
  transition: height 0.25s;
}

.App__nav--sticky{
  position: fixed;
  color: #282c34;
  top: 0px;
  width: 100%;
  z-index: 10;
  background: white;
  box-shadow: 0 0px 12px 0 rgba(0,0,0,.70);
  height: 40px;
}

.App__nav ul{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0px;
}

.App__nav ul li{
  margin: 0 20px;
  list-style: none;
  cursor: pointer;
}
.App__nav ul li:hover{
  text-decoration: underline;
}

.App__flex {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;

  background: url(/static/media/invited.57da7014.jpg) no-repeat center center;
  background-size: cover;
  padding: 1rem;
}

.activities,
.rsvp,
.activities:focus,
.rsvp:focus{
  color: white;
  text-decoration: underline;
  font-size: 2em;
}
.activities:hover,
.rsvp:hover {
  color: #efefef;
}

.activities{
  font-size: 1.5em;
}


@media screen and (max-width: 500px) and (min-width: 0px) {
  .App__nav {
    flex-wrap: wrap;
    font-size: 14px !important;

    padding: 10px 20px;
    width: 100%;
  }

  .App__flex {
    padding: 1em 0;
    overflow: hidden;
    height: 1500px;

  }

   .App__flex body {
     overflow: visible !importantx;
   }

  .App__nav ul {
    width: 100%;
  }

  .App__nav li{
    margin: 0 8px !important;
    width: auto;
  }

  .Accomodation__map {
    display: none;
  }

  .App__header h1{
    font-size: 20px;
    margin: 20px;
  }

  .Accomodation, .Wedding {
    height: calc(100vh + 200px) !important;
  }

  .Wedding .Wedding__section,
  .Wedding .Wedding__section p{
    margin-left: 20px;
    margin-right: 20px;
    width: calc(100% - 40px);
    font-size: 18px !important;
  }

  h1 {
    font-size: 28px;
  }

  h2 {
    font-size: 24px;
  }

  h3,
  h4,
  h5 {
    font-size: 22px;
  }

  h6 {
    font-size: 18px;
    font-weight: bold;
  }

  p {
    font-size: 18px !important;
  }

  .Activities__content {
    padding: 0 20px;
  }

  .App__header {
    background-position: center;
  }

  .Wedding__content{
    width: calc(100% - 20px);
    margin: 0 10px;
  }
}

.Wedding {
  border-top: 10px solid white;
  background: #282c34;
  width: 100%;
  height: 100vh;
  color: #282c34;
  display: flex;
  justify-content: center;
  flex-direction: row;

  background: #282c34;
}
.Wedding a {
  color: #282c34;
  text-decoration: underline;
}


.Wedding a:hover {
  color: #05689D;
  text-decoration: underline;
}

.Wedding__title {
  height: 80px;
}

.Wedding__times {
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  align-content: center;
}

.Wedding__content {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-content: center;

  width: 800px;
  margin: 2rem 0;

  background: rgba(255, 255, 255, 0.9);
}

.Wedding__section {
  width: 500px;
  margin: 10px 20px;
  overflow: hidden;
  align-self: center;

}

.Wedding__section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}


p {
  font-size: 24px;
}

.Travel {
  color: white;
  background: #282c34;
  width: 100%;
  height: 2200px;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
  background: url(/static/media/invited.57da7014.jpg) no-repeat center center;
  background-size: cover;

}
.Travel__mobile {
    height: 300px;
}

.Travel__modal {
  position: fixed;
  z-index: 100;
  width: 100%;
  height: 100%;
}

.Travel__title {
  height: 200px;
}

.Tourist__castle h3{
  color: white;
}

.Travel a {
  color: white;
  text-decoration: underline;

  padding: 20px 0 20px 40px;

  background: url(/static/media/pdf.5a55189b.svg) no-repeat left center;
  line-height: 30px;
  background-size: 30px;
}

.Travel__link {
  margin: 0 auto;
  width: calc(100% - 80px);
  padding: 20px;
  background: rgba(88, 88, 88, 0.6)
}

.Tourist {
  width: 100%;
  height: 100vh;
}

.TouristSlide{
  height: 500px;
  width: 900px;
  position: relative;
  left: 0;
}



.TouristSlide h3{
  color: #282c34;
}

.TouristSlide--castle{
  background: url(/static/media/kilkennycastle.27bb28b0.jpg) no-repeat center center;
  background-size: cover;
}
.TouristSlide--butlerhouse{
  background: url(/static/media/butlerhouse.7cca259b.jpg) no-repeat center center;
  background-size: cover;
}
.TouristSlide--cleeres{
  background: url(/static/media/cleeres.b1c2264f.jpg) no-repeat center center;
  background-size: cover;
}
.TouristSlide--canices{
  background: url(/static/media/canices.a6d5b918.jpg) no-repeat center center;
  background-size: cover;
}

.TouristSlide--ardmore{
  background: url(/static/media/ardmore.9bb0d66f.jpg) no-repeat center center;
  background-size: cover;
}
.TouristSlide--woodstock{
  background: url(/static/media/woodstock.008b7d3f.jpg) no-repeat center center;
  background-size: cover;
}

.TouristSlide__text {
  position: absolute;
  left: 0;
  height: calc(100% - 60px);
  width: 200px;
  background: rgba(244, 244, 244, 0.7);
  padding: 30px;
}

.TouristSlide--woodstock .TouristSlide__text{
  width: 340px;
}

.TouristSlide--right .TouristSlide__text {
   right: 0;
   left: auto;
   left: initial;
}

.TouristSlide__text p{
  font-size: 16px;
}

.Accomodation {
  width: 100%;
  height: calc(100vh + 100px);
  color: white;
  display: flex;
  justify-content: flex-start;
  flex-direction: column;
}

.Accomodation__title {
  height: 200px;
}


.Accomodation__times {
  display: flex;
  justify-content: space-around;
  flex-direction: column;
}
.Accomodation__title h1 {
  color: #282c34 !important;
}

.Accomodation__section--top h3{
  margin-top: 0;
}

.Accomodation__section a {
  color: #282c34;
  text-decoration: none;
}

.Accomodation__section a:hover{
  text-decoration: underline;
}
.Accomodation__section p{
  font-size: 16px;
}
.Accomodation__divided{
  display: flex;
  flex-direction: row-reverse;
  justify-content: center;
}
.Accomodation__section{
  color: #282c34;
  width: 50%;
}



.Accomodation__section {
  width: 300px;
  margin: 20px 20px;
  overflow: visible;

}

.Accomodation__section--top{
  margin-top: 0px;
}

.Accomodation__section img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.Activities {
  height: 100%;
  width: 100%;
  background: url(/static/media/WeddingInviteBackground.0f6d05a8.jpg) no-repeat center center;
  background-size: cover;

  display: flex;
  flex-direction: row;

  justify-content: center;

  padding: 40px 0;

  color: white;
}

.Activities h2 {
  font-size: 2.3em;
}
.Activities h3 {
  font-size: 2em;

}

.Activities h2,
.Activities h3 {
  font-weight: bold;
}

.Activities__content {
  width: 600px;
  display: flex;
  flex-direction: column;

  align-content: center;

  text-align: center;
  align-items: center;
}

.Activities__day {
  display: flex;
  flex-direction: column;
    width: 100%;
}

.Activities__event {

  margin: 35px 0;
}


.Activities hr {
  width: 100%;
  height: 1px;
  background-color: white;
}

.HR--small {
  height: 0px;
  width: 50%;
  border-width: 0;
  border: 1px dashed white;

  align-self: center;
  align-items: center;
}

.Activities a,
.Activities a:focus {
  /*color: #9bfff1;*/
  color: #abfcf7;

  font-style: italic;
  text-decoration: none;
}

.Activities a:hover {
  color: #dadada;
  text-decoration: underline;
}

.Registry {
  background: white;
  color: #282c34;
  width: 100%;
  height: auto;
  padding-bottom: 40px;
}

